// src/App.js
import React, { useState, useEffect } from 'react';
import Navbar from './components/Navbar';
import About from './components/About';
import Projects from './components/Projects';
import Skills from './components/Skills';


import './App.css';
import Contact from './components/Contact';

function App() {
    const [showSections, setShowSections] = useState({
        about: false,
        Projects: false,
      Skills: false,
        Contact: false
    });

    useEffect(() => {
        const handleScroll = () => {
            const aboutSection = document.getElementById('about')?.getBoundingClientRect();
            const projectsSection = document.getElementById('Projects')?.getBoundingClientRect();
            const skillsSection = document.getElementById('Skills')?.getBoundingClientRect();
            const contactSection = document.getElementById('Contact')?.getBoundingClientRect();

            if (aboutSection && aboutSection.top <= window.innerHeight && aboutSection.bottom >= 0) {
                setShowSections((prev) => ({ ...prev, about: true }));
            }
            if (projectsSection && projectsSection.top <= window.innerHeight && projectsSection.bottom >= 0) {
                setShowSections((prev) => ({ ...prev, Projects: true }));
            }
            if (skillsSection && skillsSection.top <= window.innerHeight && skillsSection.bottom >= 0) {
                setShowSections((prev) => ({ ...prev, Skills: true }));
            }
            if (contactSection && contactSection.top <= window.innerHeight && contactSection.bottom >= 0) {
                setShowSections((prev) => ({ ...prev, contact: true }));
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <div className="App">
            <Navbar />
            <section id="about" className={`section ${showSections.about ? 'show' : 'hide'}`}>
                <About />
            </section>
            <section id="Projects" className={`section ${showSections.Projects ? 'show' : 'hide'}`}>
                <Projects />
            </section>
            <section id="Skills" className={`section ${showSections.Skills ? 'show' : 'hide'}`}>
                <Skills />
            </section>
           
            <section id="Contact" className={`section ${showSections.Contacts ? 'show' : 'hide'}`}>
                <Contact />
            </section>
            {/* <footer className="footer">
                <div className="contact">
                    <a href="mailto:sewminidesilva4@gmail.com" target="_blank" rel="noopener noreferrer">
                        sewminidesilva4@gmail.com
                    </a>
                </div>
                <div className="contact">
                    <a href="https://www.linkedin.com/in/sewmini-de-silva-489414247/" target="_blank" rel="noopener noreferrer">
                        LinkedIn
                    </a>
                </div>
            </footer> */}
        </div>
    );
}

export default App;