// src/components/Projects.js
import React from 'react';
import '../styles/Projects.css'; // Import your CSS file for styling

const projects = [
    {
        id: 1,
        title: "Menverse E-commerce Website",
        image: "path/to/menverse-image.jpg", // Replace with your image path
        description: "A full-stack e-commerce site developed using PHP and MySQL.",
        link: "https://github.com/sewminiDeSilva/menverse" // Replace with the link to the project
    },
    {
        id: 2,
        title: "Automated Testing Framework",
        image: "path/to/automated-testing-image.jpg", // Replace with your image path
        description: "Developed an automated testing framework using Cucumber and Selenium.",
        link: "https://github.com/sewminiDeSilva/automated-testing" // Replace with the link to the project
    },
    {
        id: 3,
        title: "ToDo List Application",
        image: "path/to/todo-list-image.jpg", // Replace with your image path
        description: "A React app for organizing daily tasks with ease.",
        link: "https://github.com/sewminiDeSilva/todo-list" // Replace with the link to the project
    },
    {
        id: 4,
        title: "Virtual Art Gallery",
        image: "path/to/art-gallery-image.jpg", // Replace with your image path
        description: "An immersive virtual experience for art enthusiasts.",
        link: "https://github.com/sewminiDeSilva/virtual-art-gallery" // Replace with the link to the project
    },
    // {
    //     id: 5,
    //     title: "Weather App",
    //     image: "path/to/weather-app-image.jpg", // Replace with your image path
    //     description: "A simple weather application using React and OpenWeather API.",
    //     link: "https://github.com/sewminiDeSilva/weather-app" // Replace with the link to the project
    // },
    {
        id: 6,
        title: "Portfolio Website",
        image: "path/to/portfolio-image.jpg", // Replace with your image path
        description: "My personal portfolio built with React.",
        link: "https://sewminidesilva.github.io/Sewmini-De-Silva-Portfolio/" // Link to your portfolio
    }
];

const Projects = () => {
    return (
        <section id="projects">
            <h1>My Projects</h1>
            <div className="projects-container">
                {projects.map((project) => (
                    <div className="project-card" key={project.id}>
                        <img src={project.image} alt={project.title} className="project-image" />
                        <h2>{project.title}</h2>
                        <p>{project.description}</p>
                        <a href={project.link} className="see-more-button">See More</a>
                    </div>
                ))}
            </div>
        </section>
    );
};

export default Projects;