// src/components/Skills.js
import React, { useState, useEffect, useCallback } from 'react';
import '../styles/Skills.css'; // Import your CSS file for styling

import javaIcon from '../assets/java.png';
import phpIcon from '../assets/php.png';
import reactIcon from '../assets/react.png';
// import javascriptIcon from '../assets/javascript.png';
import mysqlIcon from '../assets/mysql.png';
// import seleniumIcon from '../assets/selenium.png';
import htmlIcon from '../assets/html.png';
// import cssIcon from '../assets/css.png';

const skills = [
    { id: 1, name: 'Java', icon: javaIcon },
    { id: 2, name: 'PHP', icon: phpIcon },
    { id: 3, name: 'React', icon: reactIcon },
    // { id: 4, name: 'JavaScript', icon: javascriptIcon },
    { id: 5, name: 'MySQL', icon: mysqlIcon },
    // { id: 6, name: 'Selenium', icon: seleniumIcon },
    { id: 7, name: 'HTML', icon: htmlIcon },
    // { id: 8, name: 'CSS', icon: cssIcon }
];

const Skills = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const skillsPerPage = 3;

    const handleNext = useCallback(() => {
        if (currentIndex < skills.length - skillsPerPage) {
            setCurrentIndex(currentIndex + skillsPerPage);
        } else {
            setCurrentIndex(0); // Loop back to the start
        }
    }, [currentIndex]);

    const handlePrev = useCallback(() => {
        if (currentIndex > 0) {
            setCurrentIndex(currentIndex - skillsPerPage);
        } else {
            setCurrentIndex(skills.length - skillsPerPage); // Loop to the end
        }
    }, [currentIndex]);

    // Automatic sliding effect
    useEffect(() => {
        const interval = setInterval(() => {
            handleNext();
        }, 3000); // Change slide every 3 seconds

        return () => clearInterval(interval); // Clear interval on unmount
    }, [handleNext]); // Include handleNext in dependencies

    return (
        <section id="skills">
            <h1>My Skills</h1>
            <div className="skills-container">
                <div className="navigation-buttons">
                    <span className="arrow" onClick={handlePrev}>&lt;</span> {/* Left arrow */}
                </div>
                {skills.slice(currentIndex, currentIndex + skillsPerPage).map((skill) => (
                    <div className="skill-card" key={skill.id}>
                        <img src={skill.icon} alt={skill.name} className="skill-icon" />
                        <h2>{skill.name}</h2>
                    </div>
                ))}
                <div className="navigation-buttons">
                    <span className="arrow" onClick={handleNext}>&gt;</span> {/* Right arrow */}
                </div>
            </div>
            <div className="progress-indicator">
                {Array.from({ length: Math.ceil(skills.length / skillsPerPage) }).map((_, index) => (
                    <span key={index} className={`dot ${index === currentIndex / skillsPerPage ? 'active' : ''}`}></span>
                ))}
            </div>
        </section>
    );
};

export default Skills;